    props.selectItems?.wbcInfoAfter.forEach(function (wbcItem: any) {
      if (lisCode.IA_CD === wbcItem.id) {
        if (lisCode.LIS_CD === 'H9600' || lisCode.LIS_CD === 'H9365' ||
