  let seq = 0;
  let kumcMergePercent = 0;
  let kumcBandPercent = 0;

  props.selectItems?.wbcInfoAfter.forEach(function (wbcItem: any) {
    if (wbcItem.id === '02' || wbcItem.id === '03' ||
