      kumcMergePercent += Number(wbcItem.percent)
    }

    if (wbcItem.id === '72') {
      kumcBandPercent = Number(wbcItem.percent)
    }
  })

  if (kumcMergePercent > 0 && kumcBandPercent < 6) {
