        orderClass.value = result.data.sort((a: any, b: any) => Number(a.orderIdx) - Number(b.orderIdx));
      }
    }
  } catch (e) {
    console.log(e)
  }
}

const beforeAfterChange = async (newItem: any) => {
  await getOrderClass();
  const filteredItems: any = await detailRunningApi(String(selectedSampleId.value));
  await store.dispatch('commonModule/setCommonInfo', {selectedSampleId: String(filteredItems?.data?.id)});
  selectItems.value = filteredItems.data;
  selectItems.value.wbcInfoAfter = newItem;
  const wbcBeforeInfo = selectItems.value.wbcInfo.wbcInfo[0];
  const wbcAfterInfo = selectItems.value?.wbcInfoAfter && selectItems.value?.wbcInfoAfter.length !== 0 ? selectItems.value?.wbcInfoAfter : filteredItems.data.wbcInfo.wbcInfo[0];
  const wbcBeforeArr = orderClass.value.length !== 0 ? orderClass.value : window.PROJECT_TYPE === 'bm' ? defaultBmClassList : defaultWbcClassList;
  const wbcAfterArr = orderClass.value.length !== 0 ? orderClass.value : window.PROJECT_TYPE === 'bm' ? basicBmClassList : basicWbcArr;
  const sortedWbcBeforeInfo = sortWbcInfo(wbcBeforeInfo, wbcBeforeArr);
  const sortedWbcAfterInfo = sortWbcInfo(wbcAfterInfo, wbcAfterArr);
  wbcInfoAfterVal.value = sortedWbcAfterInfo.filter((item: any) => !titleArr.includes(item.title));
  wbcInfoBeforeVal.value = sortedWbcBeforeInfo.filter((item: any) => !titleArr.includes(item.title));
  nonRbcClassAfterList.value = sortedWbcAfterInfo.filter((item: any) => titleArr.includes(item.title));
  nonRbcClassBeforeList.value = sortedWbcBeforeInfo.filter((item: any) => titleArr.includes(item.title));

  totalCountSet('before', wbcInfoBeforeVal.value);
  totalCountSet('after', wbcInfoAfterVal.value);

  wbcInfoBeforeVal.value.forEach((item: any) => {
    if (projectBm.value) {
      if (item.title !== 'OT') {
        const percentage = ((Number(item.count) / Number(totalBeforeCount.value)) * 100).toFixed(1);  //  0  
        item.percent = (Number(percentage) === Math.floor(Number(percentage))) ? Math.floor(Number(percentage)).toString() : percentage;
      }
    } else {
      const targetArray = getStringArrayBySiteCd(siteCd.value, selectItems.value?.testType);
      if (!targetArray.includes(item.title)) {
        const percentage = ((Number(item.count) / Number(totalBeforeCount.value)) * 100).toFixed(1); //  0  
        item.percent = (Number(percentage) === Math.floor(Number(percentage))) ? Math.floor(Number(percentage)).toString() : percentage;
      }
    }
  })

  wbcInfoAfterVal.value.forEach((item: any) => {
    if (projectBm.value) {
      if (item.title !== 'OT') {
        const percentage = ((Number(item.count) / Number(totalAfterCount.value)) * 100).toFixed(1);  //  0  
        item.percent = (Number(percentage) === Math.floor(Number(percentage))) ? Math.floor(Number(percentage)).toString() : percentage;
      }
    } else {
      const targetArray = getStringArrayBySiteCd(siteCd.value, selectItems.value?.testType);
      if (!targetArray.includes(item.title)) {
        const percentage = ((Number(item.count) / Number(totalAfterCount.value)) * 100).toFixed(1); //  0  
        item.percent = (Number(percentage) === Math.floor(Number(percentage))) ? Math.floor(Number(percentage)).toString() : percentage;
      }
    }
  })


  nonRbcClassListVal.value = [];
  wbcInfoVal.value = [];

  nonRbcClassBeforeList.value.forEach((beforeItem: any) => {
    const afterItem = nonRbcClassAfterList.value.find((afterItem: any) => afterItem.id === beforeItem.id);
    if (afterItem && !nonRbcClassListVal.value.find((item: any) => item.id === beforeItem.id)) {
      nonRbcClassListVal.value.push({
        id: beforeItem.id,
        name: beforeItem.name,
        title: beforeItem.title,
        count: { before: beforeItem.count, after: afterItem.count },
        images: { before: beforeItem.images, after: afterItem.images },
        percent: { before: beforeItem.percent, after: afterItem.percent }
      });
    }
  });

  wbcInfoBeforeVal.value.forEach((beforeItem: any) => {
    const afterItem = wbcInfoAfterVal.value.find((afterItem: any) => afterItem.id === beforeItem.id);
    if (afterItem && !wbcInfoVal.value.find((item: any) => item.id === beforeItem.id)) {
      wbcInfoVal.value.push({
        id: beforeItem.id,
        name: beforeItem.name,
        title: beforeItem.title,
        count: { before: beforeItem.count, after: afterItem.count },
        images: { before: beforeItem.images, after: afterItem.images },
        percent: { before: beforeItem.percent, after: afterItem.percent }
      });
    }
  });


  if (props.selectItems?.submitState === "") {
    const result: any = await detailRunningApi(String(props.selectItems?.id));
    const updatedItem = {
      submitState: 'checkFirst',
    };
    const updatedRuningInfo = {...result.data, ...updatedItem}
    await resRunningItem(updatedRuningInfo, true);
  }
}

const shouldRenderCategory = (title: string) => {
  //   
  const targetArray = getStringArrayBySiteCd(siteCd.value, selectItems.value?.testType);
  return !targetArray.includes(title);
};

const getStringArrayBySiteCd = (siteCd: string, testType: string): string[] => {
  if (!siteCd && siteCd === '') {
    siteCd = '0000';
    testType = '01';
  }
  const arraysBySiteCd: any = { // 0006 -> 
    '0006': {
      includesStr: ["AR", "NR", "GP", "PA", "MC", "MA", "SM", 'NE', 'GP', 'PA', 'OT'],
      includesStr2: ["NR", "AR", "MC", "MA", "SM", 'NE', 'GP', 'PA', 'OT'],
    },
  };

  //  siteCd   ,   
  const arraysForSiteCd = arraysBySiteCd[siteCd] || {
    includesStr: ["AR", "NR", "GP", "PA", "MC", "SM", "MA", 'NE', 'GP', 'PA', 'OT'],
    includesStr2: ["NR", "AR", "MC", "MA", 'NE', "SM", 'GP', 'PA', 'OT'],
  };

  // testType    
  return (testType === '01' || testType === '04') ? arraysForSiteCd.includesStr : arraysForSiteCd.includesStr2;
};

const totalCountSet = (showType: string, wbcInfoChangeVal: any) => {
  if (showType === 'before') {
    totalBeforeCount.value = 0;
  } else if (showType === 'after') {
    totalAfterCount.value = 0;
  }

  wbcInfoChangeVal.forEach((item: any) => {
    if (projectBm.value) {
      if (item.title !== 'OT') {
        showType === 'before' ? totalBeforeCount.value += Number(item.count) : totalAfterCount.value += Number(item.count);
      }
    } else {
      const targetArray = getStringArrayBySiteCd(siteCd.value, selectItems.value?.testType);
      const titleInArray = targetArray.includes(item.title);
      if (!titleInArray) {
        showType === 'before' ? totalBeforeCount.value += Number(item.count) : totalAfterCount.value += Number(item.count);
      }
    }
  });
}

async function updateOriginalDb() {
  // wbcInfo.value  (clone)   
  let clonedWbcInfo = JSON.parse(JSON.stringify([...wbcInfoAfterVal.value, ...nonRbcClassList.value]));
  let totalCount = 0;
  clonedWbcInfo.forEach((item: any) => {
    item.images.forEach((image: any) => {
      if (projectBm.value) {
        if (image.title !== 'OT') {
          totalCount += 1
        }
      } else {
        const targetArray = getStringArrayBySiteCd(siteCd.value, selectItems.value?.testType);
        if (!targetArray.includes(image.title)) {
          totalCount += 1;
        }
      }
    });
  });
  //    width height       
  clonedWbcInfo.forEach((item: any) => {
    item.images.forEach((image: any) => {
      delete image.width;
      delete image.height;
      delete image.filter;
    });
    if (projectBm.value) {
      if (item.title !== 'OT') {
        const percentage = ((Number(item.count) / Number(totalCount)) * 100).toFixed(1);  //  0  
        item.percent = (Number(percentage) === Math.floor(Number(percentage))) ? Math.floor(Number(percentage)).toString() : percentage;
      }
    } else {
      const targetArray = getStringArrayBySiteCd(siteCd.value, selectItems.value?.testType);
      if (!targetArray.includes(item.title)) {
        const percentage = ((Number(item.count) / Number(totalCount)) * 100).toFixed(1); //  0  
        item.percent = (Number(percentage) === Math.floor(Number(percentage))) ? Math.floor(Number(percentage)).toString() : percentage;
      }
    }

  });

  // wbcInfoAfter   sessionStorage 
  selectItems.value.wbcInfoAfter = clonedWbcInfo;
  await store.dispatch('commonModule/setCommonInfo', {classInfoSort: [...wbcInfoAfterVal.value, ...nonRbcClassList.value]});

  const sortArr = sortWbcInfo(orderClass.value, wbcInfoAfterVal.value);
  sortArr.forEach((item: any, index: any) => {
    item.orderIdx = index;
  });

  // originalDb 
  const res: any = await detailRunningApi(String(selectedSampleId.value));
  if (res) {
    res.data.wbcInfoAfter = clonedWbcInfo;
  }

  await putOrderClassApi(sortArr);

  //updateRunningApi 
  await updateRunningApiPost(clonedWbcInfo, [res.data]);

  emits('classOrderChanged')
  await store.dispatch('commonModule/setCommonInfo', {classInfoSort: []});
}

async function updateRunningApiPost(wbcInfo: any, originalDb: any) {
  //     
  try {
    const response = await updateRunningApi({userId: Number(userId.value), runingInfoDtoItems: originalDb})
    if (response) {

    } else {
      console.error('   ');
    }
  } catch (error) {
    console.error('Error:', error);
  }
}

