    if (newData.length !== 0) {
      for (const item of newData) { //   
        const {fullNm, abbreviation, customNum} = item;
        const filePath = `${iaRootPath.value}/${selectItems.value?.slotId}/${projectTypeReturn(projectType.value)}/${customNum}_${abbreviation}`;
