          images: [],
          title: abbreviation,
        };

        const foundObject = wbcinfo.find((wbcItem: any) => wbcItem.id === wbcPush.id && wbcItem.name === wbcPush.name);
        if (!foundObject) {
          wbcinfo.push(wbcPush);
          wbcInfo.value = wbcinfo;
          await updateOriginalDb('notWbcAfterSave');
